.order-form-switch {
  flex-grow: 1;
  max-width: fit-content;
  margin-right: 20px;
  margin-bottom: 20px;
  padding: 16px 16px;
  background-color: var(--gray-4);
  border: 1px solid var(--gray-5);
  border-radius: 5px;
  transition: border-color var(--main-transition), background-color var(--main-transition);
}
.order-form-switch-box {
  margin-bottom: 20px;
}
.order-form-switch label {
  display: inline-block;
  position: relative;
  font-size: 18px;
  padding-left: 40px;
  cursor: pointer;
}
.order-form-switch label::before {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  content: "";
  width: 24px;
  height: 24px;
  border: 2px solid var(--gray-6);
  border-radius: 50%;
  transition: border-color var(--main-transition);
}
.order-form-switch label::after {
  position: absolute;
  top: 50%;
  left: 8px;
  transform: translateY(-50%);
  content: "";
  width: 8px;
  height: 8px;
  background-color: var(--accent);
  border-radius: 50%;
  opacity: 0;
  transition: opacity var(--main-transition);
}
.order-form-switch input[type=radio] {
  display: none;
}
.order-form-switch.bx-selected {
  border: 1px solid var(--accent);
  border-radius: 5px;
  background-color: var(--white);
}
.order-form-switch input[type=radio]:checked + label::before {
  border: 2px solid var(--accent);
}
.order-form-switch input[type=radio]:checked + label::after {
  opacity: 1;
}

.form-field-box {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 20px;
  margin-bottom: 30px;
}
.form-field-box-section {
