}
.form-field-box fieldset label.required::before {
  position: relative;
  top: 0;
  left: 0;
  content: "*";
  color: var(--accent);
}
.form-field-box fieldset input[type=text],
.form-field-box fieldset textarea {
  padding: 12px;
  margin-bottom: 10px;
  border: 1px solid var(--gray-5);
  border-radius: 5px;
}
.form-field-box fieldset textarea {
  resize: none;
  min-height: 200px;
  height: 100%;
}

